.homepage-header {
  position: relative;
  background-repeat: no-repeat;
  background-size: cover;
  text-align: center;
  height: 500px;
  padding: 20px 0;
  color: white; }
  @media (min-width: 640px) and (max-width: 1023px) {
    .homepage-header {
      height: 800px; } }
  .homepage-header.slide1 {
    background-image: url("./images/what_we_do_img.png");
    background-position: center; }
  .homepage-header.slide2 {
    background-image: url("./images/who_we_are_image.png");
    background-position: center; }
  .homepage-header.slide3 {
    background-image: url("./images/aboutus-img.png");
    background-position: center; }
  .homepage-header .slide-content {
    position: absolute;
    top: 8rem;
    left: 3rem; }
    .homepage-header .slide-content p {
      width: 80%;
      background: red;
      color: #fff;
      padding: 1rem;
      text-align: left; }

.subpage .subpage-header h1 {
  position: relative;
  top: 1rem; }
  @media (max-width: 500px) {
    .subpage .subpage-header h1 {
      top: 3rem; } }

.App-header .nav {
  list-style: none;
  margin-top: 0.8rem; }
  .App-header .nav li {
    margin-right: 1rem;
    display: inline-block; }
    .App-header .nav li a {
      font-size: 16px;
      color: #000; }

.content.home {
  position: relative;
  top: 2rem;
  margin-bottom: 5rem; }
  @media (min-width: 640px) and (max-width: 1023px) {
    .content.home {
      position: absolute;
      top: 54rem; } }

.building {
  position: absolute;
  top: 0;
  left: 45px;
  z-index: 2;
  background-color: #fff;
  width: auto;
  height: 45px; }
  @media (min-width: 1024px) {
    .building {
      left: 1rem;
      top: 0.3rem; } }

.App-logo {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 2;
  background-color: rgba(255, 255, 255, 0.8);
  width: auto;
  height: 45px;
  padding: 0.5rem; }
  @media (min-width: 1024px) {
    .App-logo {
      top: 0.3rem;
      right: 1rem; } }

.slick-slider {
  top: 0;
  left: 0;
  width: 100%; }

.bm-burger-button {
  background: url("./images/icon.svg");
  background-size: contain;
  position: absolute;
  width: 45px;
  height: 45px;
  left: 0;
  top: 0;
  cursor: pointer;
  background-color: #000;
  border: 0.5rem solid black; }
  @media (min-width: 1024px) {
    .bm-burger-button {
      display: none; } }

.bm-cross-button {
  background: url("./images/cross.svg");
  background-size: contain;
  color: #fff;
  cursor: pointer;
  width: 20px !important;
  height: 20px !important;
  right: auto;
  left: 1rem !important;
  top: 1rem !important; }

.bm-menu-wrap {
  background: #000;
  position: fixed;
  right: 0;
  z-index: 1100;
  width: 100% !important;
  height: 100%;
  -webkit-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
  -webkit-transform: translate3d(0px, 0px, -100%);
          transform: translate3d(0px, 0px, -100%); }
  .bm-menu-wrap .mobile-nav {
    top: 5rem;
    left: 2rem; }
    .bm-menu-wrap .mobile-nav li {
      text-align: left;
      padding: 0.8rem 0; }
      .bm-menu-wrap .mobile-nav li a {
        color: #acacac;
        font-size: 25px; }
      .bm-menu-wrap .mobile-nav li .nav-divider {
        width: 50px;
        border-bottom: 1px solid #acacac;
        left: 1rem;
        position: relative;
        top: 0.8rem; }

p, span, div, li, a {
  font-size: 1.2rem; }



